@import '../vars';

$title-offset: 50%;
$hover-offset: 1em;

.card {
    margin-bottom: 2em;

    position: relative;
    width: 100%;
    
    &:hover .title {
        left: calc(#{$title-offset} + #{$hover-offset});
    }

    &:hover .images {
        left: -$hover-offset;
        .title {
            
            left: calc(#{$title-offset} + #{$hover-offset * 2})
        }
    }


    .images {
        position: absolute;
        top: 0;
        left: 0;

        transition: left 0.2s;

        z-index: 1;

        width: 100%;
        height: 10em;

        mask-image: linear-gradient(120deg, rgba(255,255,255,1), rgba(255,255,255,1) 50%, rgba(255,255,255,0) 50%);

        cursor: pointer;
        user-select: none;
        overflow: hidden;

        img {
            width: 100%;
        }

        .title {
            color: $cd-blue-300;
            mix-blend-mode: color-dodge;
        }
    }

    .title {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        left: $title-offset;
        top: 5em;
        width: 90%;
        text-align: center;

        transition: left 0.2s;
        h1 {
            font-size: 3.5em;
        }

    }
    
    

    .content-container {
        display: flex;
        align-items: center;
        justify-content: center;

        
        .content {
            max-width: 90%;
            width: 90%;
            text-align: justify;
        }

        margin-top: 10em;
    }
    


}


