@import '../vars';

$height: 20em;

.content-container {
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.content {
    position: relative;
    flex: 1 1 auto;
    
    max-width: 90%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

p {    
    @media only screen and (max-width: 600px) {
        font-size: 1em !important;
    }
    text-align: justify;
}

.content h1 {
    font-size: 3em;
}

.article-title {
    $title-offset: 50%;
    $hover-offset: 1em;

    width: 100%;
    height: 10em;
    margin-bottom: 1em;

    position: relative;

    &:hover .title-text {
        left: calc(#{$title-offset} + #{$hover-offset});
    }

    &:hover .title-image {
        left: -$hover-offset;
        .title-text {
            left: calc(#{$title-offset} + #{$hover-offset * 2})
        }
    }


    .title-image {
        position: absolute;
        top: 0;
        left: 0;

        transition: left 0.2s;

        z-index: 1;

        width: 100%;
        height: 10em;

        mask-image: linear-gradient(120deg, rgba(255,255,255,1), rgba(255,255,255,1) 50%, rgba(255,255,255,0) 50%);

        user-select: none;
        overflow: hidden;
        
        img {
            width: 100%;
        }

        .title-text {
            color: $cd-blue-300;
            mix-blend-mode: color-dodge;
        }
    }

    .title-text {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        left: $title-offset;
        top: 5em;
        width: 90%;
        text-align: center;

        transition: left 0.2s;
        h1 {
            font-size: 3.5em;
        }

    }
    
}